import Link from "next/link";
import { Container } from "../container";
import { ArrowRightIcon } from "@/assets/icons";

const HeaderTop = () => {
  return (
    <div className="w-full bg-cerulean h-10">
      <Container className="flex justify-center items-center h-full">
        <p className="font-normal text-sm leading-[21px] text-whiteOut flex">
          Корзина неавторизованных пользователей хранится 7 дней. Пожалуйста, {" "}
          <Link href="/" className="flex ml-0.5">
            <p className="underline">авторизуйтесь</p>
            <span>
              <ArrowRightIcon />
            </span>
          </Link>
        </p>
      </Container>
    </div>
  );
};

export default HeaderTop;
